import React, { Component } from 'react';

import flats from '../data/flats.js';
import FlatList from './flat_list.jsx';
import GoogleMapReact from 'google-map-react';
import Marker from './marker.jsx';

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      selectFlat: [],
      flats
    };
  }
  
  selectFlat = (lat) => {
    this.setState({
      selectFlat: lat
    });
  }

  center = () => {
    const Lat = (this.state.selectFlat[0] ? this.state.selectFlat[0] : 48.85);
    const Lng = (this.state.selectFlat[-1] ? this.state.selectFlat[-1] : 2.35);
    return {
      lat: Lat,
      lng: Lng
    };
  }
  
  render() {
    return (
      <div>
        <FlatList flats={this.state.flats} selectFlat={this.selectFlat}/>

        <div className="map-container">          
          <GoogleMapReact defaultCenter={this.center()} defaultZoom={12}>
            <Marker lat={this.center().lat} lng={this.center().lng} />
          </GoogleMapReact>
        </div>
      </div>
    );
  }
}

export default App;
